@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* typography */
html {
  font-size: 62.5%;
  line-height: 1.5;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.5rem;
  background-color: hsl(240, 78%, 98%);
  background-color: var(--color-light);
  background-image: url(/pricing-component-with-toggle/static/media/bg-bottom.0ed1debd.svg);
  background-position: bottom left;
  background-repeat: no-repeat;
  min-height: 100vh;
  position: relative;
}

body::before {
  content: "";
  background-image: url(/pricing-component-with-toggle/static/media/bg-top.4980c1fd.svg);
  background-position: top right;
  background-repeat: no-repeat;
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  z-index: -1;
  -webkit-transform: translateX(20rem);
          transform: translateX(20rem);
}

@media screen and (min-width: 768px) {
  body::before {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

:root {
  --gradient-primary: linear-gradient(
    to bottom right,
    hsl(236, 72%, 79%),
    hsl(237, 63%, 64%)
  );
  --color-primary: hsl(232, 13%, 33%);
  --color-secondary: hsl(233, 13%, 49%);
  --color-accent: hsl(234, 14%, 74%);
  --color-light: hsl(240, 78%, 98%);
  --color-white: #fff;
}

/* Global */
.clickable {
  cursor: pointer;
}

/* Toggler Component */
.toggler-container {
  --height: 3rem;
  background-image: linear-gradient(
    to bottom right,
    hsl(236, 72%, 79%),
    hsl(237, 63%, 64%)
  );
  background-image: var(--gradient-primary);
  border-radius: 2rem;
  height: var(--height);
  width: 7rem;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 0.5rem;
  position: relative;
}

.toggler {
  height: calc(var(--height) - 0.7rem);
  width: calc(var(--height) - 0.7rem);
  border-radius: 50%;
  background-color: hsl(240, 78%, 98%);
  background-color: var(--color-light);
}

.toggler-position--left {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.toggler-position--right {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

/* Pricing Header */
.pricing-header {
  text-align: center;
  margin: 7rem 0;
}

.pricing-heading {
  color: hsl(232, 13%, 33%);
  color: var(--color-primary);
}

.pricing-toggler-group {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.toggler-cycle {
  color: hsl(233, 13%, 49%);
  color: var(--color-secondary);
  margin: 0 2rem;
}

/* Card */
.card {
  background-color: #fff;
  background-color: var(--color-white);
  max-width: 400px;
  border-radius: 1rem;
  text-align: center;
  color: hsl(232, 13%, 33%);
  color: var(--color-primary);
  padding: 5rem 3rem;
  margin: 4rem 2rem;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.card:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
}

.card--primary {
  background-image: linear-gradient(
    to bottom right,
    hsl(236, 72%, 79%),
    hsl(237, 63%, 64%)
  );
  background-image: var(--gradient-primary);
  color: hsl(240, 78%, 98%);
  color: var(--color-light);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.card__title {
  font-size: 1.6rem;
  margin: 0;
  padding: 2rem 0 1rem;
  opacity: 0.8;
}

.card__subtitle {
  font-size: 6rem;
  margin: 0;
}

.list {
  list-style-type: none;
  padding: 0;
}

.list-item {
  border-bottom: 1px solid hsl(234, 14%, 74%);
  border-bottom: 1px solid var(--color-accent);
  padding: 2rem;
  font-weight: 700;
  opacity: 0.8;
}

.list-item:first-child {
  border-top: 1px solid hsl(234, 14%, 74%);
  border-top: 1px solid var(--color-accent);
}

/* button component */
.btn {
  border: 1px solid transparent;
  padding: 1.5rem;
  width: 100%;
  border-radius: 1rem;
  margin-top: 2rem;
  outline: 0;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 1.5rem;
  letter-spacing: 2px;
  font-weight: 700;
}

.btn--primary {
  background-image: linear-gradient(
    to bottom right,
    hsl(236, 72%, 79%),
    hsl(237, 63%, 64%)
  );
  background-image: var(--gradient-primary);
  color: hsl(240, 78%, 98%);
  color: var(--color-light);
}

.btn--primary:hover {
  border: 1px solid hsl(232, 13%, 33%);
  border: 1px solid var(--color-primary);
  background-image: none;
  background-color: hsl(240, 78%, 98%);
  background-color: var(--color-light);
  color: hsl(232, 13%, 33%);
  color: var(--color-primary);
}

.btn--secondary {
  background-color: hsl(240, 78%, 98%);
  background-color: var(--color-light);
  border: 1px solid hsl(240, 78%, 98%);
  border: 1px solid var(--color-light);
  color: hsl(232, 13%, 33%);
  color: var(--color-primary);
}

.btn--secondary:hover {
  background-image: linear-gradient(
    to bottom right,
    hsl(236, 72%, 79%),
    hsl(237, 63%, 64%)
  );
  background-image: var(--gradient-primary);
  border: 1px solid hsl(240, 78%, 98%);
  border: 1px solid var(--color-light);
  color: hsl(240, 78%, 98%);
  color: var(--color-light);
}

/* Pricing Body */
.pricing-body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.pricing-body .card {
  -webkit-flex: 1 1 100%;
          flex: 1 1 100%;
}

@media screen and (min-width: 1024px) {
  .pricing-body {
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
  }
}

/* ToolTip */
.tool-tip {
  position: absolute;
  top: -35px;
  background-color: #fff;
  background-color: var(--color-white);
  text-align: center;
  box-shadow: 5px 5px 10px hsl(233, 13%, 49%);
  box-shadow: 5px 5px 10px var(--color-secondary);
  border-radius: 0.5rem;
  padding: 0.2rem 2rem;
  max-height: 0;
  opacity: 0;
  transition: all 0.2s ease;
  width: 300px;
  display: inline-block;
}
.toggler-container:hover .tool-tip {
  max-height: 100%;
  opacity: 0.8;
}

